$grey: #c0c0c0;
$pink: #f95656;
$dark-grey: #4c4c4c;

.container{
  width: 960px;
  margin: 0 auto;
  padding: 0px 10px;
}

.btn{
	padding: 20px 25px;
	text-transform: uppercase;
	font-size: 15px;
	line-height: 17px;
	background: $pink;
	color: #fff;
	outline: none;
	border: none;
	border-bottom: 2px solid #b84040;
	transition: .4;
	&:hover{
		background: #fb6f6f;
	}
}

.block-ttl{
	font-size: 24px;
	line-height: 29px;
	color: #535353;
	margin-bottom: 15px;
	text-transform: uppercase;
	font-weight:700;
}
.data, .author{
	font-size: 15px;
	line-height: 18px;
	font-weight: 200;
}
small{
	span{
		font-size: 15px;
		line-height: 18px;
		font-weight: 400;
		color:#939393;
		text-transform: lowercase;
	}
}
.clr{
	display: block;
	content: '';
	clear: both;
}