
header{
	font-family: Regular;
	min-width: 960px;
	min-height: 495px;
	background: #eeeef4 url(../images/header-bg.jpg) center center no-repeat;
	padding: 15px 0px;
	background-size: cover;
	.wrapper-header{
		background-color: uppercase;;
		width: 67%;
		.logo{
			width: 251px;
			height: 51px;
			margin-bottom: 15px;
			a{
				display: block;
				width: 100%;
				height: 100%;
				img{
					width: 100%;
				}
			}
		}
		.contacts-header{
			margin-bottom: 15px;
			li{
				display: inline-block;
				margin-left: 15px;
				&:first-child{
					margin-left: 0;
				}
				span{
					font-size: 18px;
					line-height: 40px;
					color: #535353;
					font-weight: 400;
					vertical-align: middle;
				}
				img{
					vertical-align: middle;
				}
			}
		}
		.navigation{
			padding: 19px 25px;
			border: 3px solid $pink;
			margin-bottom: 55px;
			ul{
				li{
					display: inline-block;
					margin-right: 24px;
					text-transform: uppercase;
					font-size: 13px;
					line-height: 16px;
					&:last-child{
						margin-right: 0;
					}
					&.active{
						a{
							color: $pink;
						}
					}
					a{
						color: #545454;
						&:hover{
							text-decoration: underline;
						}
					}
				}
			}
		}
		.header-ttl{
			text-transform: uppercase;
			color: #535353;
			font-size: 40px;
			line-height: 46px;
			// font-family: Semibold;
			font-weight: 600;
			margin-bottom: 15px;
		}

	}
}
.wrapper{
	font-family: Regular;
	min-width: 960px;
	padding: 35px 0px;
	background-color: #eeeef4;
	.container{
		text-align: center;
		.left-column{
			text-align: left;
			width: 57%;
			float: left;
			.last-materials{
				padding: 0px 0px 30px;
				.block-ttl{
					margin-bottom: 8px;
				}
				ul{
					.item-material{
						display: inline-block;
						width: 49.5%;
						padding: 5px;
						&:nth-of-type(1),&:nth-of-type(3){
							padding-left: 0;
						}
						&:nth-of-type(2),&:nth-of-type(4){
							padding-right: 0;
						}
						&:hover{
							a{
								img{
									opacity: 1;
								}
							}
						}
						.icon{
							width: 15px;
						}
						a{
							display: block;
							width: 100%;
							height: 100%;
							position: relative;
							img{
								width: 100%;
								font-size: 0;
								opacity: .9;
								// transition: .4s;
							}
							.material-photo-ttl{
								position: absolute;
								top: calc(50% - 24px);
								left: 30px;
								font-size: 24px;
								line-height: 28px;
								text-transform: uppercase;
								color: #fff;
								display: block;
								padding-right: 20px;
							}
							small{
								position: absolute;
								bottom: 17px;
								left:30px;
								.data{
									color: #fff;
								}
							}
						}
					}
				}
			}
			
			.articles{
				.articles-wrapper{
					width: 100%;
					background-color: #fff;
					article{
						padding: 20px 30px 25px;
						border-bottom: 2px dotted #8d8d8d;
						.block-ttl{
							margin-bottom: 0px;
						}
						.author{
							color: $pink;
							font-weight: 400;
							margin-bottom: 15px;
							margin-right: 10px;
							display: inline-block;
						}
						.data{
							color: #939393;
						}
						.text{
							font-size: 16px;
							line-height: 18px;
							color: #4c4c4c;
							margin-bottom: 15px;
							font-weight: 500;
						}
						.bottom-line{
							width: 100%;
							margin-bottom: 20px;
							font-size: 15px;
							line-height: 18px;
							.more{
								display: inline-block;
								color: $pink;
								text-decoration: underline;
								margin-right: 15px;
								&:hover{
									text-decoration: none;
								}
							}
							small{
								margin-right: 15px;
								span{
									font-size: 15px;
									line-height: 18px;
									font-weight: 400;
									color:#939393;
									text-transform: lowercase;
								}
							}
						}
						.form-wrap{
							display: block;
							padding: 30px;
							background: $pink;
							.block-ttl{
								font-size: 18px;
								line-height: 24px;
								color: #fff;
								display: block;
								margin-bottom: 15px
							}
							form{
								input{
									background: #be3333;
									height: 50px;
									padding: 0px 10px;
									margin-bottom: 13px;
									border: none;
									width: 100%;
									color: #fff;

								}
								::-webkit-input-placeholder {
								  	color: #fff;
    								font-family: Semibold;

								}
								::-moz-placeholder {
								  	color: #fff;
    								font-family: Semibold;

								}
								:-ms-input-placeholder {
								  	color: #fff;
    								font-family: Semibold;

								}
								input:-moz-placeholder {
								  	color: #fff;
    								font-family: Semibold;

								}
								.btn{
									height: 50px;
									padding: 0px;
									width: 100%;
									background: #fff;
									color: $pink;
								}
							}
						}
					}
				}
				.pager{
					display: block;
					width: 100%;
					padding: 15px 30px;
					background-color: #fff;
					>span{
						display: inline-block;
						&:hover{
							a{
								color: $pink;
							}
						}	
						a{
							padding: 1px 6px;
							font-size: 15px;
							color: #555555;
							width: 100%;	
							&.active{
								border: 1px solid $pink;
								color: $pink;
							}
						}
					}
				}
			}
		}
		.right-column{
			text-align: left;
			width: 42%;
			float: left;
			padding: 0px 15px;
			.aside-wrapper{
				background-color: #fff;
				padding: 5px 0px;
				margin-bottom: 30px;
				.item-aside{
					padding: 25px 15px;
					border-bottom: 2px dotted #8d8d8d;
					&:last-child{
						border-bottom: none;
					}
					img,.description-aside{
						display: inline-block;
						vertical-align: top;
					}
					img{
						margin-right: 20px;
					}
					.description-aside{
						max-width: 65%;
						.item-aside-ttl{
							text-transform: uppercase;
							font-size: 14px;
							color: #4c4c4c;
							margin-bottom: 13px;
						}
						.author{
							color: $pink;
							display: block;
						}
						.views{
							margin-right: 7px;
							img,span{
								vertical-align: middle;
							}
							img{
								margin-right: 0px;
							}
						}

					}

				}
			}
			.social{
				ul{
					.item-social{
						width: 49.3%;
						padding: 8px;
						display: inline-block;
						a{
							width: 100%;
							display: block;
							padding: 15px;
							&.fb{
							background-color: #3a5795;
							opacity: .9;
							&:hover{
								opacity: 1;
							}
							}
							&.twitter{
								background-color: #41abe1;
								opacity: .9;
								&:hover{
									opacity: 1;
								}
							}
							&.google{
								background-color: #ce6868;
								opacity: .9;
								&:hover{
									opacity: 1;
								}
							}
							&.vk{
								background-color: #4c75a3;
								opacity: .9;
								&:hover{
									opacity: 1;
								}
							}
						}
						&:nth-of-type(1),
						&:nth-of-type(3){
							padding-left: 0;
						}
						&:nth-of-type(2),
						&:nth-of-type(4){
							padding-right: 0;
						}
						&:nth-of-type(1),
						&:nth-of-type(2){
							padding-top: 0;
						}
						&:nth-of-type(3),
						&:nth-of-type(4){
							padding-bottom: 0;
						}
						img{
							display: inline-block;
							vertical-align: middle;
							margin-right: 35px;
							margin-bottom: 10px;
						}
						.folowers{
							display: inline-block;
							vertical-align: middle;
							text-transform: uppercase;
							color: #fff;
						}
						.bootom-line{
							.text{
								font-size: 14px;
								color:#fff;
							}
							img{
								width: 21px;
								height: 21px;
								margin: 0px 0px 0px 10px;
							}
						}
					}
				}
			}
		}
	}
}
footer{
	font-family: Regular;
	min-width: 960px;
	background-color: #4c4c4c;
	color: #c0c0c0;
	padding: 40px 0px 45px;
	text-align: center;
	.footer-ttl{
		font-size: 16px;
		line-height: 19px;
		margin-bottom: 18px;
		text-transform: uppercase;	
	}	
	.footer-logo, .footer-contacts, .footer-call-back{
		float: 	left;	
		width: 32%;	
		text-align: left;
	}
	.footer-logo{
		margin-top: 67px;
	}
	.footer-contacts{
		ul{
			.item-footer-contacts{
				margin-bottom: 	25px;
				img{
					margin-right: 10px;
					vertical-align: middle;
				}
				span{
					vertical-align: middle;
				}
			}
		}
	}
	.footer-call-back{
		.footer-ttl, input{
			margin-bottom: 9px;
		}
		form{
			width: 100%;
			input{
				background: #373535;
				padding: 12px 15px;
				border:none;
				width: 100%;
			}
			::-webkit-input-placeholder {
				color:#c0c0c0;
				font-size: 15px;
			}
			.btn{
				width: 100%;
			}
		}
	}
}